main {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    min-height: 100%;
    padding: 26px;
    background-color: #222;
    font-family: 'Arial';
}

div {
    color: #fff;
}

img {
    width: 70%;
}

input {
    width: 100%;
    height: 56px;
    padding: 0 16px;
    color: #fff;
    background-color: #444;
    border: none;
    border-radius: 6px;
    outline: none;
}

span {
    margin-top: 35px;
    text-align: center;
    color: #fff;
    font-size: 14px;
}

.town {
    margin-top: 0;
    margin-bottom: 35px;

    color: #D4AF37;
}

button {
    height: 48px;
    margin-top: 35px;
    padding: 0 26px;
    border: 4px solid #D4AF37;
    border-radius: 28px;
    color: #fff;
    background: transparent;
}